.promo .swiper-pagination {
  bottom: 15px;
}

.promo .swiper-pagination-bullet {
  width: 25px;
  height: 5px;
  border-radius: 3px;
  border: none;
  background: white;
  opacity: 1;
}

.promo .swiper-pagination-bullet-active {
  background: #fff06b;
}

.promo .swiper-button-prev,
.promo .swiper-button-next {
  display: none;
  width: 3rem;
  height: 3rem;
  font-size: 1.5rem;
  font-weight: bold;
  color: #000000;
  background-color: #fff;
  border-radius: 50%;
}

.promo .swiper-button-prev:after,
.promo .swiper-button-next:after {
  font-size: inherit;
}

.promo .swiper-button-prev:hover,
.promo .swiper-button-next:hover {
  color: #fff;
  background-color: #fff06b;
}

@media (min-width: 640px) {
  .promo .swiper-button-prev,
  .promo .swiper-button-next {
    display: flex;
  }

  .promo .swiper-pagination {
    bottom: 30px;
  }

  .promo .swiper-pagination-bullet {
    width: 50px;
    height: 5px;
  }
}

.slide-pic {
  position: relative;
}

.slide-pic img {
  width: auto;
  object-fit: contain;
  margin-bottom: -23px;
  height: auto;
  margin-top: auto;
}

.slide-pic::before {
  z-index: -1;
  content: '';
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-65%, -50%);
  width: 100%;
  height: 100%;
  background: radial-gradient(
    100% 100% at center,
    transparent 0,
    #ffffff,
    transparent 76.56%
  );
  border-radius: 50%;
  overflow: hidden;

  filter: blur(50px);
}
