[class^='StepperContainer'] {
  padding: 0 !important;
  width: 100%;
}

[class^='StepButton-'].active {
  @apply bg-primary !important;
}

[class^='StepButton-'].completed {
  @apply bg-primary !important;
}

.stepper-form-wrapper label::after {
  content: '';
}

input[type='date'] {
  width: 100%;
  display: block;
  background: transparent;
  background-color: transparent;
}
