.header {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 125% */
  /* @apply text-primary; */
  color: var(--color-h-tariff);
  @apply bg-h-tariff-1 border-h-tariff-1 text-primary;
}

@media (min-width: 960px) {
  .header {
    font-size: 16px;
  }
}

[data-theme="theme1"] .header {
  @apply bg-gradient-to-r from-[#6DDCFF] to-[#7F60F9];
}
