.title {
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%; /* 33.6px */
}

@media (min-width: 567px) {
  .title {
    font-size: 56px;
    font-style: normal;
    font-weight: 700;
    line-height: 110%; /* 61.6px */
  }
}

@media (min-width: 960px) {
  .title {
    font-size: 60px;
    font-style: normal;
    font-weight: 700;
    line-height: 110%; /* 66px */
  }
}
