
.btn {
  color: white;
  border: 2px solid;
  border-color: transparent;
  @apply bg-primary;
}

.btn:hover {
  background-color: transparent;
  @apply text-primary border-primary;
}

.btnOutline {
  border: 2px solid;
  background-color: transparent;
  @apply text-primary border-primary;
}

.btnOutline:hover {
  border-color: transparent;
  color: white;
  @apply bg-primary;
}

[data-theme="theme1"] .btn {
  @apply bg-gradient-to-r from-[#6DDCFF] to-[#7F60F9];
}

[data-theme="theme1"] .btn:hover {
  background: transparent;
  @apply text-[#7F60F9] border-[#7F60F9];
}

[data-theme="theme1"] .btnOutline {
  border: 2px solid;
  background: transparent;
  @apply text-[#7F60F9] border-[#7F60F9];
}

[data-theme="theme1"] .btnOutline:hover {
  border-color: transparent;
  color: white;
  @apply bg-gradient-to-r from-[#6DDCFF] to-[#7F60F9];
}
